import React from "react";

import './Sidebar.css';
import Contact from "../contact/contact";

function Sidebar() {

  const [showMenu, setHideMenu] = React.useState(false);
  const [selectMenu, setSelectMenu] = React.useState('');

    return (
      <div id="sidebar_container" className="sidebar-container" onClick={(event)=>{
          if(event.target.id==='sidebar_container')setHideMenu(false);
        }}>
        {showMenu &&
        <div className={"sidebar"+(showMenu ? " open" : '')+(selectMenu ? ' large' : '')} id="mySidebar">
          <div className="menus">
            <a href="#work" className="bar-item">Work</a>
            <a href="#about" className="bar-item">About</a>
          <div className="bar-item">Contact us on</div>
          <Contact></Contact> 
          </div>
        </div>}
        <button className="menu" onClick={()=>setHideMenu(!showMenu)}>{showMenu ?'X' : ''}</button>
      </div>
    );
}


export default Sidebar;