
import React from 'react';

import './ImageSource.css'

function ImageSource(props) {

  const [loading, setLoading] = React.useState(true);
    
  const [items, setItems] = React.useState(null);
  const [title, setTitle] = React.useState('loading');
  const imgRef = React.useRef();

  React.useEffect(()=>{
    // 
    // if(loading) getImage();
    setTitle('');
    // props.item.url = props.item.url;
    setItems(props.item);
    setTitle(props.item.key);
    // getImage(props.item);
  },[props.item]);

    // async function getImage(url) {
    //   return fetch('https://ak-akshaykale.github.io/'+url.url).then((img)=>{
    //     // setLoading(false)
    //     if(img.status===200){
    //     props.item.url = 'https://ak-akshaykale.github.io/'+props.item.url;
    //     setItems(props.item);
    //     setTitle(props.item.key);
    //     }
    //   })

    // }


    return (title &&
      <div key={'imgBox-'+title} className="imgBox">
        {loading &&
          <img
          key={'img-'+title+'loading'}
          src={'/loading.gif'}
          alt={''}
          style={{
            width: "100%",
            height: "100%",
            overflow: "hidden",
            objectFit: 'cover'
          }}
        />
        }        
          <img
            key={'img-'+title}
            ref={imgRef}
            src={items?.url}
            alt={''}
            style={{
              width: "100%",
              height: "100%",
              overflow: "hidden",
              objectFit: 'cover'
            }}
            hidden={!loading && !(imgRef.current.complete)}
            onLoad={()=>setLoading(false)}
          />                
          <div key={'box-text-'+title} className='hovertext'>
          {title?.split('.')[0]}
        </div>
      </div>
   );
}


export default ImageSource;