import React from "react";

import './ImageSlidder.css';
import Carousel from "framer-motion-carousel";
import Message from "../Message/Message";
import InitLoad from "../InitLoad/InitLoad";

function ImageSlidder(props) {

  const [showMenu, setHideMenu] = React.useState(true)
  const [showItems, setShowItems] = React.useState('');
  const [items, setItems] = React.useState('')

  React.useEffect(()=>{
    // 
    // if(loading) getImage();
    if(props.selectedImage){
      setShowItems([props.selectedImage]);
    }
    setItems(props.items);
  },[props]);



    return (showMenu &&
      <div className="image-slider-container">
        <div className="image-slider" onClick={()=>setShowItems(items)}>
        {props.showLoading && <InitLoad onClose={setHideMenu && props.onClose} />}
        {props.showChat && <Message onClose={setHideMenu && props.onClose} />}
        {showItems?.length > 0 && 
        <Carousel className="imgs-carousel" autoPlay={false} onClick={()=>setShowItems(items)}>
          {showItems.map((item, i) => (
            <img
              draggable="false"
              src={item.url}
              key={item.title}
              width="100%"
              style={{ objectFit: 'cover' }}
              alt=""
            />
          ))}
        </Carousel>
        }
        </div>
        <button className="close-btn" onClick={()=>{
          props.onClose();
          setHideMenu(!showMenu)
          }}>X</button>
      </div>
    );
}


export default ImageSlidder;