
import React from 'react';

import { useWindowSize } from "react-use";
import { InfiniteGrids } from 'infinite-grids';
import './ImageViewer.css'
import ImageSlidder from '../ImageSlidder/ImageSlidder';
import ImageSource from '../ImageSource/ImageSource';

function ImageViwer(props) {

  const [loading, setLoading] = React.useState(true);
    
  const [items, setItems] = React.useState(null);
  const [fullViewItem, setFullViewItem] = React.useState(props.items[0]);
  const { width } = useWindowSize();

  const numberOfColumns =
    width && width < 350
      ? 1 
      : width < 420
      ? 2
      : width && width > 420 && width < 768
      ? 3
      : width && width > 768 && width < 1024
      ? 4
      : 5;

    React.useEffect(()=>{
      // 
      // if(loading) getImage();
      setItems(props.items);
    },[props.items]);

    function getImage(url) {
      return fetch(url).then((img)=>{
        // setLoading(false)
        return url;
      })

    }

  //   <div key={'box-'+props.ikey} className='box'>
  //   <div key={'imgBox-'+props.ikey} className="imgBox">
  //     {/* {
  //       loading ?
  //         (<img key={'img-'+props.ikey} src={'/loading.gif'} alt={'loading'} width={100}/>) : */}
      
      
  //     <img key={'img-'+props.ikey} src={props.url} alt={props.title?.split('.')[0]} width={400}/>                
  //       <div key={'box-text-'+props.ikey} className='hovertext'>
  //       {props.title?.split('.')[0]}
  //     </div>
  //   </div>
  // </div>
  

    return (
      <div style={{ padding: 16 }}>
        {items &&
        <InfiniteGrids
          items={items}
          // loadMore={loadMore}
          horizontalGap={16}
          verticalGap={16}
          renderItem={(item) => (
            <div key={'box-'+props.title} className='box' onClick={()=>{
              setFullViewItem(item);
              props.onView(item);
            }}>
            <ImageSource item={item} />
          </div>
          )}
          loader={<h4>{''}</h4>}
          numberOfColumns={numberOfColumns}
        />
    }
    </div>
   );
}


export default ImageViwer;


