

import React from 'react';
import './App.css';
import ImageViwer from './ImageView/ImageViewer';
import logo from './logo.svg'
import Sidebar from './Sidebar/Sidebar';
import ImageSlidder from './ImageSlidder/ImageSlidder';



function App() {

  const [imgs, setImgs] = React.useState([]);
  const [selectedImage, setSelectedImage] = React.useState(null);  
  const [showFullImages, setShowFullImages] = React.useState(false); 
  const [showChat, setShowChat] = React.useState(false);
  const [showLoading, setShowLoading] = React.useState(true);  
  
  React.useEffect(()=>{
    const newImages = importAll(require.context('/public/gallary', true, /\.(png|PNG|JPG?G|jpe?g|svg|apng|bmp|gif|webp)$/));
    setImgs(newImages.reverse());
    // setImgs([{ key:'fst', url : '/static/media/Project K.326837ea868124a0f592.jpg', title : 'project'}])
    // setItems(newImages.reverse());
    
    window.addEventListener("load", (event) => {
      // console.log("page is fully loaded");
      setShowLoading(false);
    });
    window.onload = ()=> setShowLoading(false);
  },[]);

  function importAll(r) {
    let images = [];
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });

    Object.keys(images).forEach((key)=>{
      images.push({key : key, url : window.location.origin +images[key], title : key});
    });
    return images;
  }

  function onView(item){
    console.log(item);
    setSelectedImage(item);
    setShowFullImages(true);
  }

  function onFullScreenClose(){
    setShowFullImages(false);
  }

  return (
    <div className={"App"+(showLoading ? ' ui-block' : '')}>
      <Sidebar />
      {showLoading && <ImageSlidder showLoading={true} onClose={setShowLoading}/>}
      {showFullImages && <ImageSlidder selectedImage={selectedImage} items={imgs} onClose={onFullScreenClose}/>}
      {showChat && <ImageSlidder showChat={true} onClose={setShowChat}/>}
      <div className='logo'>
          <img className='brand' src={logo} width={100} alt='garuda'/>
          <span className='title'>Garuda Production</span>
          <span className='sub-title'>Photography | Videography</span>
      </div>
      <div className='container'>
        {/* {imgs.length > 0 ? imgs.map((item,index)=>{
            return (<ImageViwer ikey={item.key+index} url={item.url} title={item.title} />);
          }) : <span className='no-photos'>No Photographs uploaded yet </span>
        } */}
        {
          <ImageViwer items={imgs} onView={onView}/>
        }
      </div>
      <div id = "chat-animated" class = "chat-animated bounceInLeft" onClick={()=>setShowChat(!showChat)}>
        <img className='chat-icon' src={logo} width={58} alt='garuda'/>
      </div>
      <footer>
        <span className='copyright'> 2018</span>
        <a className='powered-brand' href='http://elakshami.in'  target="_blank" rel="noreferrer">
          <span>Powered by</span>  <img className='chat-icon' src={'/elakshami_small.png'} width={150} height= {50} alt='elakshami'/></a>
      </footer>
    </div>
  );
}

export default App;
