import React from "react";
import "./InitLoad.css"


import loadIcon from "../logo.svg"
import loadIcon2 from "../l5.svg"

function InitLoad(props) {
    const [counter, setCounter] = React.useState(0);
  
    React.useEffect(()=>{
      setTimeout(()=>{
        props.onClose(false);
      },30000);
    })

    return (
    <div className="loading-container">
      <img
      className="brand-cls"
        draggable="false"
        src={loadIcon}
        key={'init-loading-brand'}
        alt=""
        width={'150px'}
        onClick={()=>{
          setCounter(counter+1);
          if(counter+1 > 2){
            props.onClose(false);
          }
        }}
      />
      <img
              draggable="false"
              src={loadIcon2}
              key={'init-loading'}
              alt=""  width={'150px'}
            />
    </div>);
}


export default InitLoad;